.header {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100px; /* Здесь можно указать высоту вашего хедера */
    padding: 0; /* Убираем все отступы */
    margin: 0;
    box-sizing: border-box;
}

.header-logo-container {
    width: 100%;
    height: 100%; /* Логотип растягивается на всю высоту */
}

.header-logo {
    width: 100%;
    height: 100%; /* Логотип растягивается на всю ширину и высоту */
    object-fit: contain; /* Чтобы логотип не терял пропорции */
}

nav {
    display: flex;
    gap: 20px;
}

.header-link {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    background-color: #3498db;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.header-link:hover {
    background-color: #2980b9;
}


